import { NgModule } from "@angular/core";
import { RouterModule, Routes } from "@angular/router";

import { VoiceSendComponent } from "./voice-send/voice-send.component";
import { VoiceReportComponent } from "./voice-report/voice-report.component";
import { VoiceLogsComponent } from "./voice-logs/voice-logs.component";
import { VoiceViewComponent } from "./voice-view/voice-view.component";
import { VoiceComposeComponent } from "./voice-compose/voice-compose.component";
import { AuthGuard } from "src/app/auth.guard";

const routes: Routes = [
  {
    path: "voice_send",
    component: VoiceSendComponent,
    canActivate: [AuthGuard],
  },
  {
    path: "voice_compose",
    component: VoiceComposeComponent,
    canActivate: [AuthGuard],
  },
  {
    path: "voice_report",
    component: VoiceReportComponent,
    canActivate: [AuthGuard],
  },
  {
    path: "voice_logs/:id",
    component: VoiceLogsComponent,
    canActivate: [AuthGuard],
  },
  {
    path: "voice_statistics/:id",
    component: VoiceViewComponent,
    canActivate: [AuthGuard],
  },
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule],
})
export class VoiceRoutingModule {}
