import { Component, OnInit } from "@angular/core";
import { VoiceServiceService } from "src/app/voice/voice-service.service";
import { HttpClient } from "@angular/common/http";
import {
  FormBuilder,
  FormControl,
  FormGroup,
  Validators,
} from "@angular/forms";
import { NgxCsvParser } from "ngx-csv-parser";
import { NgxCSVParserError } from "ngx-csv-parser";
import { SmsServiceService } from "src/app/shared/sms-service.service";
import { NgbModal, ModalDismissReasons } from "@ng-bootstrap/ng-bootstrap";
import * as moment from "moment";

@Component({
  selector: "app-voice-send",
  templateUrl: "./voice-send.component.html",
  styleUrls: ["./voice-send.component.css"],
})
export class VoiceSendComponent implements OnInit {
  searchedKeyword_p: string;
  pb_data: [];
  sendVoice: FormGroup;
  selectedLang: string = "";
  isChecked: boolean;
  voicelist: [];
  first_row: any;
  cSV: any[] = [];
  total_no_con: number;
  final_aray1 = [];
  duration = 0;
  credit = 0;
  selected: any = "";
  minDate: any;
  maxDate: any;
  currentDay = moment().day();
  showDateTime: boolean;
  currentTime = moment().format("hh:mm A");

  //event handler for the select element's change event
  selectedRoute(event: any) {
    this.selectedLang = event.target.value;
    console.log(event.target.value);
    this.isChecked = !this.isChecked;
  }
  ///select file
  selectedVoiceFile(event: any, voicelist: any) {
    console.log(event);

    console.log(voicelist);
    this.duration = voicelist.duration;
    this.credit = Math.floor(voicelist.duration / 30 + 1);
    // this.sendVoice.patchValue({
    //   draft_id: event.target.value,
    // });
  }

  ///select phonebook for msg send and send to sms compoent
  public phoneb_id = [];
  phoneb_name = [];
  selectedPhonebook(details: any, check: any) {
    console.log(check.target.name);
    var name = details.phonebook_name;
    var id = details.phonebook_id;
    if (check.target.checked == true) {
      this.phoneb_id.push(id);
      this.phoneb_name.push(name);
    } else {
      this.phoneb_id = this.phoneb_id.filter((phoneb_id) => phoneb_id !== id);
      this.phoneb_name = this.phoneb_name.filter(
        (phoneb_name) => phoneb_name !== name
      );
    }
    // var data = [this.phoneb_id, this.phoneb_name];
    // console.log("sms", data);
    // console.log("sms", this.phoneb_name);
  }

  constructor(
    public fb: FormBuilder,
    public voiceService: VoiceServiceService,
    private ngxCsvParser: NgxCsvParser,
    public smsService: SmsServiceService,
    private modalService: NgbModal
  ) {}

  ngOnInit(): void {
    //send Voice form
    this.sendVoice = this.fb.group({
      campaign_name: [, [Validators.required]],
      sender: [, [Validators.required]],
      receivers: [],
      route: ["PR", [Validators.required]],
      draft_id: [, [Validators.required]],
      csv_numbers: [],
      phonebook_id: [],
    });

    //get user phonebooklist
    this.smsService.info().subscribe(
      (ress: any) => {
        this.pb_data = ress.data;
      },
      (error) => {
        if (error.status === 401) {
          // this.jwtService.logout();
        }
      }
    );
    //get voice list
    this.voiceService.getVoiceList().subscribe(
      (res: any) => {
        console.log(res);
        this.voicelist = res;
      },
      (error) => {
        console.log(error);

        // }
      }
    );
  }

  onSubmit() {
    console.log(this.sendVoice.value);
    this.sendVoice.value["scheduleTime"] = this.selected;
    console.log(this.sendVoice.value);

    this.voiceService.sendVoice(this.sendVoice.value).subscribe(
      (res: any) => {
        if (res) {
          // alert(res.message);
          console.log(res);
        }
      },
      (error) => {
        console.log(error);

        // }
      },
      () => {
        this.sendVoice.reset({
          route: "PR",
        });
      }
    );
  }

  //csv upload for direct send msg
  arr_mobile = [];
  fileChangeListener($event: any): void {
    const files = $event.srcElement.files;
    console.log(files);
    if (files) {
      this.final_aray1 = [];
    }

    this.ngxCsvParser
      .parse(files[0], { header: false, delimiter: "," })
      .pipe()
      .subscribe(
        (result: Array<any>) => {
          this.cSV = result;
          // let result2 = result;
          // if (result[0].length > 15) {
          //   alert("You can upload CSV file have less than 15 Column");
          // } else {
          console.log("Result", result);
          if (result) {
            //  this.showImport = true;
            this.first_row = result[0];
            // var removefirstRow = result2.shift();
          }
          // }

          /////get mobile number from csv///////
          // for (var i = 0; i < result.length; i++) {
          //   for (var j = 0; j < result[i].length; j++) {
          //     var mobile_no = result[i][j];
          //     // var phoneno = /^\d{10}$/;
          //     var phoneno =
          //       /^[\+]?[(]?[0-9]{3}[)]?[-\s\.]?[0-9]{3}[-\s\.]?[0-9]{4,6}$/im;
          //     // var phoneno = " (?<!\\d)\\d{10}(?!\\d)";
          //     //  /^\d{10}$/;
          //     if (mobile_no.match(phoneno)) {
          //       this.arr_mobile.push(mobile_no);
          //       console.log("contact_from_csv", this.arr_mobile);
          //       // this.total_no_con = this.arr_mobile.length;
          //     }
          //   }
          // }
        },
        (error: NgxCSVParserError) => {
          console.log("Error", error);
        }
      );
  }
  selectedCol(e, val) {
    console.log(e.target.value);
    // this.sendToThisColumn=e.target.value;
    console.log(val);
    this.final_aray1 = [];
    for (var i = 1; i < this.cSV.length; i++) {
      // if (field_id == "template_id") {
      var array1 = {};

      // (array1["no"] = i),
      // this.selectedColumnForNumberSend = e.target.value;

      var a = this.cSV[i][e.target.value];

      this.final_aray1.push(a);
      // }
    }

    console.log(this.final_aray1);
  }

  sendSelectedNumber() {
    console.log("ff", this.final_aray1);
    // var mm = this.arr_mobile.length;
    if (this.final_aray1.length > 0) {
      this.total_no_con = this.final_aray1.length;
      // var select = this.arr_mobile;
      // console.log("con", select);
    } else {
      alert("select any column of csv for send SMS");
    }
  }

  setDefaultValue() {
    this.sendVoice.patchValue({
      phonebook_id: this.phoneb_id,
      csv_numbers: this.final_aray1,
    });
  }

  //set condition for past date disable
  updateTime(e) {
    this.showDateTime = true;
    this.minDate = moment(e);
    if (e) {
      var scheduleDateTime = moment(e.startDate._d).format(
        "DD/MM/YYYY hh:mm A"
      );

      this.selected = scheduleDateTime;

      console.log(scheduleDateTime);
    }

    //this.maxDate = moment(e).add(15,'days');
  }

  // open(content) {
  //   this.modalService.open(content, { size: "lg" });
  // }
  openMd(content) {
    this.modalService.open(content, { size: "md" });
  }
}
