import { Component, OnInit } from "@angular/core";
import { VoiceServiceService } from "src/app/voice/voice-service.service";
import { GlobalConstants } from "src/app/global_constant";
import Swal from "sweetalert2/dist/sweetalert2.js";

@Component({
  selector: "app-voice-compose",
  templateUrl: "./voice-compose.component.html",
  styleUrls: ["./voice-compose.component.css"],
})
export class VoiceComposeComponent implements OnInit {
  url_image = GlobalConstants.url_image;
  audiofile = "";
  voicelist: [];
  audio_file: any;
  title: any;
  duration: any;
  constructor(private voiceService: VoiceServiceService) {}

  ngOnInit(): void {
    this.voiceService.getVoiceList().subscribe(
      (res: any) => {
        console.log(res);
        this.voicelist = res;
      },
      (error) => {
        console.log(error);

        // this.jwtService.error(error);
      }
    );
  }

  handleFiles(event) {
    console.log(event);
    const file1 = event.target.files[0];

    var files = event.target.files;
    console.log(files);
    this.audio_file = file1;

    var a = URL.createObjectURL(files[0]);
    console.log(a);

    this.audiofile = a;
    // this.sanitizer.bypassSecurityTrustResourceUrl
    // var aa = readAsDataUrl(files[0]);
    // this.audiofile = aa;
    console.log(a);

    // document.getElementById("voiceCallfile_audio").load();
  }
  saveAudioFile() {
    console.log(this.title);
    console.log(this.duration);
    console.log(this.audio_file);

    const formData = new FormData();
    formData.append("voice_draft", this.audio_file);
    formData.append("title", this.title);
    formData.append("duration", this.duration);

    this.voiceService.saveAudioFile(formData).subscribe(
      (res) => {
        alert(res.message);
        this.ngOnInit();
        console.log(res);
      },
      (error) => {
        console.log(error);
      }
    );
  }
  deleteSavedAudio(data) {
    console.log(data);
    Swal.fire({
      title: "Are you sure?",
      text: "You won't be able to revert this!",
      icon: "warning",
      showCancelButton: true,
      confirmButtonColor: "#3085d6",
      cancelButtonColor: "#d33",
      confirmButtonText: "Yes, delete it!",
    }).then((result) => {
      if (result.isConfirmed) {
        this.voiceService.deleteSavedAudio(data.draft_id).subscribe(
          (res: any) => {
            this.ngOnInit();
            console.log(res);
          },
          (error) => {
            console.log(error);

            // this.jwtService.error(error);
          }
        );
      }
    });
  }
}
