import { Injectable } from "@angular/core";
import { HttpClient } from "@angular/common/http";
import { GlobalConstants } from "src/app/global_constant";

@Injectable({
  providedIn: "root",
})
export class VoiceServiceService {
  url = GlobalConstants.url;

  constructor(private http: HttpClient) {}

  ////////////////----------------------send voice start---------------------///////////

  public getVoiceList() {
    return this.http.get<any>(this.url + "get_draft");
  }
  public sendVoice(data) {
    return this.http.post<any>(this.url + "sendVoice", data);
  }

  ////////////////----------------------send voice end---------------------///////////

  ////////////////----------------------report start---------------------///////////
  //get voice report
  public getReportVoice() {
    return this.http.get<any>(this.url + "voiceReport");
  }

  //filtter or search report
  public searchVoiceReport(data) {
    return this.http.post<any>(this.url + "voiceFilterReport", data);
  }

  public voiceReportExport(data) {
    return this.http.post<any>(this.url + "voiceReportExport", data);
  }
  ////////////////----------------------report end---------------------///////////

  ////////////////----------------------logs start---------------------///////////
  public voiceLogs(id: any) {
    return this.http.get<any>(this.url + "voiceLogs/" + id);
  }
  public resendVoice(data) {
    return this.http.post<any>(this.url + "resendVoice", data);
  }
  public searchVoiceLogs(data: any) {
    return this.http.post<any>(this.url + "voiceLogsFilter", data);
  }

  public voiceLogsExport(data) {
    return this.http.post<any>(this.url + "voiceLogsExport", data);
  }
  ////////////////----------------------logs end---------------------///////////

  ////////////////----------------------view start---------------------///////////
  public voiceStatistics(id: any) {
    return this.http.get<any>(this.url + "voiceStatistics/" + id);
  }
  ////////////////----------------------view end---------------------///////////

  ////////////////----------------------compose start---------------------///////////
  public saveAudioFile(data) {
    return this.http.post<any>(this.url + "save_draft", data);
  }

  public deleteSavedAudio(id: any) {
    return this.http.get<any>(this.url + "delete_draft/" + id);
  }
  ////////////////----------------------compose end---------------------///////////
}

