import { Component, OnInit, ViewChild } from "@angular/core";
import { Location } from "@angular/common";
import { VoiceServiceService } from "src/app/voice/voice-service.service";
import * as moment from "moment";
import { DaterangepickerDirective } from "ngx-daterangepicker-material";
import { ngxCsv } from "ngx-csv/ngx-csv";

@Component({
  selector: "app-voice-report",
  templateUrl: "./voice-report.component.html",
  styleUrls: ["./voice-report.component.css"],
})
export class VoiceReportComponent implements OnInit {
  voice_report: [];
  selected: any = [
    moment().format("DD-MM-YYYY"),
    moment().format("DD-MM-YYYY"),
  ];
  maxDate: any;
  alwaysShowCalendars: boolean;
  showReportOnMobile: boolean;
  showReportNormal: boolean;
  //pagination
  p: number = 1;
  count: number = 10;
  @ViewChild(DaterangepickerDirective, { static: false })
  pickerDirective: DaterangepickerDirective;

  constructor(
    public location: Location,
    private voiceService: VoiceServiceService
  ) {}

  ngOnInit(): void {
    this.alwaysShowCalendars = true;
    this.showReportOnMobile = false;
    this.showReportNormal = true;

    this.voiceService.getReportVoice().subscribe(
      (res: any) => {
        this.voice_report = res.data;
        console.log(res);
      },
      (error) => {
        if (error.status == 401) {
          alert(error.statusText);
        }
        console.log(error);

        // this.jwtService.error(error);
      }
    );
  }

  change(val: any) {
    this.maxDate = moment(val);
    var start = moment(val.startDate._d).format("DD-MM-YYYY");
    var end = moment(val.endDate._d).format("DD-MM-YYYY");
    console.log(start);
    console.log(end);
    this.selected = [start, end];
  }

  searchReport(val: any) {
    console.log(val);

    this.voiceService.searchVoiceReport(val).subscribe(
      (res: any) => {
        console.log(res);
        if (res.status == 201) {
          if (res.data[0].receivers) {
            console.log("data with receivers");
            this.voice_report = res.data;
            this.showReportOnMobile = true;
            this.showReportNormal = false;
          } else if (res.data.receivers == null) {
            console.log("data without receivers or normal");
            this.voice_report = res.data;
            this.showReportOnMobile = false;
            this.showReportNormal = true;
          }
        } else if (res.status == 201 || res.data.length == 0) {
          alert("no data found");
          console.log("no data found");
        }
      },
      (error) => {
        if (error.status == 401) {
          // this.jwtService.error(error);
        }
      }
    );
  }
  clearFormValue(searchForm) {
    console.log(searchForm);
    searchForm.reset({
      route: "",
      type: "",
      size: "",
      date: [moment().format("DD-MM-YYYY"), moment().format("DD-MM-YYYY")],
      number: "",
      campaign_id: "",
      campaign_name: "",
    });
    this.ngOnInit();
  }

  exportReport(val: any) {
    console.log(val);
    this.voiceService.voiceReportExport(val).subscribe(
      (res: any) => {
        console.log(res);
        if (res.status == 201) {
          console.log(res.data);

          let i = 0;
          let csvFileName = "voicereport" + moment().format("YYYYMMDDSSMMHH");
          let finalData = res.data.map(function (obj) {
            return {
              No: (i = i + 1),
              CampaignName: obj.campaign_name,
              CampaignUid: obj.campaign_id,
              CallerID: obj.sender,
              FileTitle: obj.message,
              TotalCredit: obj.credit,
              Receivers: obj.receivers,
              Status:
                obj.dlr_mask == 923
                  ? "Submitted"
                  : obj.dlr_mask == 2
                  ? "Failed"
                  : obj.dlr_mask == 1
                  ? "Delivered"
                  : obj.dlr_mask == 8
                  ? "Sent"
                  : // : obj.dlr_mask == 5
                    // ? "Rejected&Block"
                    "Rejected&Block",
              SubmitDate:
                obj.submit_date !== null ? obj.submit_date : "Update Soon",
              DoneDate: obj.done_date !== null ? obj.done_date : "Update Soon",
            };
          });
          console.log(finalData);
          var options = {
            fieldSeparator: ",",
            quoteStrings: '"',
            decimalseparator: ".",
            showLabels: false,
            showTitle: false,
            // title: 'CSV DATA',
            useBom: true,
            noDownload: false,
            headers: [
              "S.No",
              "CampaignName",
              "CampaignUid",
              "CallerID",
              "FileTitle",
              "TotalCredit",
              "Receivers",
              "Status",
              "SubmitDate",
              "DoneDate",
            ],
          };
          new ngxCsv(finalData, csvFileName, options);
        } else if (res.status == 202) {
          ///if data greater than limit schedule for csv file by backend
          // Swal.fire({
          //   position: "top",
          //   icon: "info",
          //   title: res.message,
          //   showConfirmButton: false,
          //   timer: 2000,
          // });

          alert(res.message);
        }
      },
      (error) => {
        // this.jwtService.error(error);
      }
    );
  }
}
